<template>
  <div id="app" class="colums">
    <TopMemu/>
    <div class="main-content columns is-fullheight">
      <SideMenu/>
      <div id="app-container" class="container column is-9">



 <!-- <HelloWorld/> -->
    

        <router-view/>


<div id="backtotop">
<button class="goTop" v-if="isVisible" @click="backToTop">
        <!-- <i class="fa fa-angle-up" aria-hidden="true"></i> -->
        <font-awesome-icon icon="angle-up" />
    </button>
</div>



      </div>
    
    



  </div></div>
</template>

<script>
// import HelloWorld from './components/HelloWorld.vue'
import SideMenu from '@/components/SideMenu.vue'
import TopMemu from '@/components/TopMemu.vue'

import JQuery from 'jquery'
let $ = JQuery

export default {
  name: 'app',
  template: '#backtotop',
  components: {
    // HelloWorld,
    SideMenu,
    TopMemu
  },
  data() {
    return {
      isVisible: false
    }
  },
//   head: {
// title: {
//   inner: 'SPH The Code of Ethics'
// }
//   },
  methods: {
    initToTopButton: function() {
      $(document).bind('scroll', function() {
        var backToTopButton = $('.goTop');
        if ($(document).scrollTop() > 250) {
          backToTopButton.addClass('isVisible');
          this.isVisible = true;
        } else {
          backToTopButton.removeClass('isVisible');
          this.isVisible = false;
        }
      }.bind(this));
    },
    backToTop: function() {
      $('html,body').stop().animate({
        scrollTop: 0
      }, 'slow', 'swing');
    }
  },
  mounted(){
    this.$nextTick(function() {
      this.initToTopButton();
    });
  }
  


}
  







</script>





